<script lang="ts">import { parseElement, changeAppearance } from "./CenterNote.svelte";
window.addEventListener("DOMContentLoaded", () => {
    // 初期画面はエディターなのでタイトルもEditorにしておく
    document.title = "SLOUCH - Editor";
    // 二つのボタンを取得してnullだったら返しておく
    const editButton = document.getElementById("Editor");
    if (!editButton)
        throw "editButton is null";
    const previewButton = document.getElementById("Preview");
    if (!previewButton)
        throw "previewButton is null.";
    // エディターボタンがクリックされた時
    editButton.addEventListener("click", () => {
        triggerAntinomyButtons(editButton, previewButton);
        changeAppearance(0);
    });
    // プレビューボタンがクリックされた時
    previewButton.addEventListener("click", () => {
        triggerAntinomyButtons(previewButton, editButton);
        parseElement();
        changeAppearance(1);
    });
});
// 押されたボタンをdisabledにして他方のdisabledを削除する
// disabledにしないと、すでに押してある方を押した際の無駄な条件分岐が発生する
// disabledにすることでSCSSの指定が楽になると言うメリットもある
const triggerAntinomyButtons = (clicked, another) => {
    clicked.setAttribute("disabled", "true");
    // 押されたボタンのIDを取得してタイトルにぶち込む
    const idName = clicked.getAttribute("id");
    if (!idName)
        throw "idName is null";
    document.title = `SLOUCH - ${idName}`;
    // 他方のボタンがdisabledだったらそれを削除する
    if (another.hasAttribute("disabled"))
        another.removeAttribute("disabled");
};
</script>

<div class="editor-button-group">
  <button class="editor-button fas fa-eye" id="Preview"></button>
  <button class="editor-button fas fa-pencil-alt" id="Editor" disabled></button>
</div>

